import Vue from "vue";
import VueRouter from "vue-router";
import Aboutme from "../views/Aboutme";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "Aboutme",
        component: Aboutme,
    },
    {
        path: "/animation",
        name: "animation",

        component: () =>
            import ( /* webpackChunkName: "animation" */ "../views/Animation.vue"),
    },
    {
        path: "/transform",
        name: "transform",

        component: () =>
            import ( /* webpackChunkName: "transform" */ "../views/transform.vue"),
    },
    {
        path: "/transition",
        name: "transition",

        component: () =>
            import ( /* webpackChunkName: "transition" */ "../views/transition.vue"),
    },
    {
        path: "/vuetify",
        name: "vuetify",

        component: () =>
            import ( /* webpackChunkName: "vuetify" */ "../views/vuetify.vue"),
    },
    {
        path: "/variables",
        name: "Variables",

        component: () =>
            import ( /* webpackChunkName: "Variables" */ "../views/Variables.vue"),
    },
];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes,
});

export default router;