<template>
  <div class="navigation">
    <h2>Разделы:</h2>
    <div class="css main-link">
      <div @click.prevent="navitems[0].show = !navitems[0].show">
        {{ navitems[0].name }}
      </div>
      <ul v-if="navitems[0].show">
        <li
          v-for="(item, index) in css"
          :key="index"
          :data-url="item.url"
          class="nav-item"
          @click="goto"
        >
          <router-link :to="item.url"> {{ item.name }}</router-link>
        </li>
      </ul>
    </div>
    <div class="html main-link">
      <div @click="navitems[1].show = !navitems[1].show">
        {{ navitems[1].name }}
      </div>
      <ul v-if="navitems[1].show"></ul>
    </div>
    <div class="java-script main-link">
      <div @click="navitems[2].show = !navitems[2].show">
        {{ navitems[2].name }}
      </div>
      <ul v-if="navitems[2].show">
        <li
          v-for="(item, index) in javascript"
          :key="index"
          :data-url="item.url"
          class="nav-item"
          @click="goto"
        >
          <router-link :to="item.url"> {{ item.name }}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "navigation",
  data: () => ({
    css: [
      { index: 0, name: " Трансформация ", url: "/transform" },
      { index: 1, name: " Transition ", url: "/transition" },
      { index: 2, name: " vuetify ", url: "/vuetify" },
      { index: 3, name: " Анимация ", url: "/animation" },
    ],
    javascript: [
      {
        index: 0,
        name: " Декларативное и иперативное программирование ",
        url: "/variables",
      },
    ],
    navitems: [
      { name: "CSS", show: false },
      { name: "HTML", show: false },
      { name: "JavaScript", show: false },
    ],

    currentURL: "1234",
  }),
  methods: {
    goto() {
      this.currentURL = event.target.closest("li").dataset.url;
      this.$emit("url", this.currentURL);
    },
  },
};
</script>

<style lang="scss" scoped>
.main-link {
  cursor: pointer;
  color: black;
  font-size: 1.2em;
}
li {
  text-align: right;
  list-style: none;
  margin-right: 20%;
  a {
    font-size: 0.8em;
  }
  a:visited {
    color: rgb(158, 44, 161);
  }
}
</style>
